<template>
  <Card v-loading="loadingShow.items.all">
    <template #header>
      <div class="flex justify-between items-center">
        <h6>Data Match Items</h6>
        <Button color="teal" dense @click="openModal('create')"><i class="material-icons mr-2">add</i>Add Item</Button>
      </div>
    </template>
    <template #default>
      <ItemTable scan-in buyback-type="mailin" />
    </template>
  </Card>
  <Modal v-model="modal.create" title="Add Item" @close="unsetData">
    <template #default>
      <DeviceForm ref="refDeviceForm" v-model:data="device.data" v-model:dataComputer="device.computer" multi-imei buyback-type="mailin" />
    </template>
    <template #footer>
      <Button color="teal" :loading="loading.create" @click="addDevice">Add Item</Button>
    </template>
  </Modal>
</template>

<script>
import { defineComponent, nextTick, ref } from 'vue'
import { useShowItems, loadingShow } from 'composable/buyback'
import { useRoute } from 'vue-router'
import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import ItemTable from 'components/Buyback/ItemTable.vue'
import DeviceForm from 'components/Device/Form.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Card,
    Button,
    Modal,
    ItemTable,
    DeviceForm
  },
  async setup(){
    const { device, modal, unsetData, openModal, loading, actionCreate } = useShowItems('online')
    const route = useRoute()
    const refDeviceForm = ref(null)
    const addDevice = async () => {
      const isValid = await nextTick(() => refDeviceForm.value.validate())
      if(isValid) await actionCreate(route.params.buybackId, async () => {
        ElMessage({
          type: 'success',
          message: 'Item added'
        })
      }, true)
    }
    return {
      refDeviceForm,
      loading,
      device,
      modal,
      unsetData,
      openModal,
      actionCreate,
      loadingShow,
      addDevice
    }
  }
})
</script>