<template>
  <Card v-loading="loading">
    <template #header>
      <h6>Buyback ID #{{state?.box_id}}</h6>
    </template>
    <template #default>
      <div v-if="!!state?.box_id" class="my-4">
        <div class="flex items-center mb-2">
          <span class="material-icons text-base mr-2">date_range</span>
          <p class="items-center">{{ state?.created_at || '-' }}</p>
        </div>  
        <Badge :color="color(state?.order_status?.short_name || '-')">{{ state?.order_status?.status_name || '-'}}</Badge>
      </div>
    </template>
  </Card>
</template>

<script>
import { defineComponent, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import { useShow } from 'composable/buyback'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Badge from 'bbUi/components/badge/badge.vue'
import { color } from 'utils/status'

export default defineComponent({
  components: {
    Button,
    Card,
    Badge
  },
  props: {
  },
  setup(props) {
    const { action, state, loading } = useShow('online')
    const route = useRoute()
    onMounted(async () => {
      await action({
        id: route.params.buybackId,
        ...(
          (route.params.marketerId && route.params.marketerId === 'me' && { marketer_id: '' })
          || (route.params.marketerId && { marketer_id: route.params.marketerId })
        )
      })
    })
    return {
      state,
      loading,
      color
    }
  }
})
</script>
