<template>
  <Card>
    <template #header>
      <h6>Items</h6>
    </template>
    <template #default>
      <div>
        <div class="grid grid-cols-5 gap-4 mb-4">
          <div class="col-span-3">
            <Input v-model="filters.search" placeholder="Search items" block />
          </div>
          <div class="col-span-2">
            <Select v-model="filters.condition" placeholder="Condition" class="w-full" :items="stateConditions" by="condition_name" name="condition_name" :loading="loadingCondition.all" />
          </div>
        </div>
        <div class="overflow-y-auto min-h-max" :style="{maxHeight: '450px', minHeight: '60px'}" v-loading="loading.all">
          <div class="mt-6" v-if="!!state.data?.length">
            <div v-for="(device, deviceIndex) of state.data" :key="deviceIndex" class="flex justify-between items-center mb-8">
              <div>
                <p class="font-semibold text-teal-04 mb-3">{{ device.product_name }}</p>
                <div class="flex justify-between">
                  <p class="mr-3">Qty: <span class="font-bold">{{ device.qty }}</span></p>
                  <p class="mr-3">Price: <span class="font-bold">{{ device.price }}</span></p>
                  <p class="mr-3">Condition: <span class="font-bold">{{ device.condition }}</span></p>
                </div>
              </div> 
              <div>
                <Button @click="handleQuickAdd(device)" color="teal" dense>Quick Add</Button>
              </div>
            </div>
          </div>
          <div v-else class="mt-8 mb-4">
            <p class="font-semibold text-center">No data</p>
          </div>
        </div>
      </div>
    </template>
  </Card>
  <Modal v-model="modal.create" title="Quick Add Item" @close="unsetSelectedDevice">
    <template #default>
      <DeviceForm ref="refDeviceForm" auto-fill v-model:data="data.selected.device.detail" v-model:device="data.selected.device.specs.device" v-model:dataComputer="data.selected.device.specs.computer" multi-imei />
    </template>
    <template #footer>
      <div class="flex justify-end">
        <Button  @click="handleAddDevice" :loading="loading.create" color="teal" view="outlined">Add</Button>
      </div>
    </template>
  </Modal>
</template>

<script>
import { defineComponent, nextTick, onMounted, watch, ref } from 'vue'
import { useRoute } from 'vue-router'
import { useShowDevices, useShowItems } from 'composable/buyback'
import { useInventoryCondition } from 'composable/setting'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Badge from 'bbUi/components/badge/badge.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import DeviceForm from 'components/Device/Form.vue'
import debounce from 'utils/debounce'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Button,
    Card,
    Badge,
    Input,
    Select,
    Modal,
    DeviceForm
  },
  props: {
  },
  setup(props) {
    const { loading, state, filters, modal, data, actionShow, actionCreate, setSelectedDevice, unsetSelectedDevice } = useShowDevices()
    const { actionAll: actionItems }  = useShowItems('online')
    const { actionAll: actionConditions, stateAll: stateConditions, filter: conditionFilters, loading: loadingCondition } = useInventoryCondition()
    const route = useRoute()
    const refDeviceForm = ref(null)

    const handleQuickAdd = (device) => {
      setSelectedDevice(device)
      modal.create = true
    }
    const handleAddDevice = async () => {
      const isValid = await nextTick(() => refDeviceForm.value.validate())
      if(isValid){
        await actionCreate(route.params.buybackId, async () => {
          ElMessage({
            type: 'success',
            message: 'Item added'
          })
          modal.create = false
          await actionItems(route.params.buybackId, true)
        })
      }
    }

    watch(() => filters, debounce(() => {
      actionShow(route.params.buybackId)
    }, 500), {
      deep: true
    })
    onMounted(async () => {
      conditionFilters.is_notpaginate = 1
      await Promise.all([
        actionShow(route.params.buybackId),
        actionConditions()
      ])
    })
    return {
      refDeviceForm,
      data,
      stateConditions,
      state,
      loading,
      loadingCondition,
      filters,
      modal,
      handleQuickAdd,
      handleAddDevice,
      unsetSelectedDevice
    }
  }
})
</script>
