<template>
  <div>
    <div class="flex justify-center justify-between">
      <Breadcrumb class="mb-12" page-title="Data Match" :backTo="{name: 'buybackOnlineDetail', params: {
        buybackId: route.params.buybackId
      }}" :items="breadcrumb" />
      <div>
        <Button @click="handleRestore" class="mr-4" color="orange" view="outlined" :loading="loading.restore"><i class="material-icons mr-1">replay</i> Restore</Button>
        <Button @click="handleSave" color="teal" view="outlined" :loading="loading.save"><i class="material-icons mr-1">save</i> Save</Button>
      </div>
    </div>
    <div class="grid gap-6 grid-cols-8">
      <div class="col-span-8 xl:col-span-2 lg-col-span-2">
        <Show class="mb-8" />
        <Devices />
      </div>
      <div class="col-span-8 xl:col-span-6 lg-col-span-6">
        <Items />
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useDataMatch, useShowItems } from 'composable/buyback'
import Breadcrumb from 'components/Breadcrumb.vue'
import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import Show from './detail/Show.vue'
import Devices from './detail/Devices.vue'
import Items from './detail/Items.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Breadcrumb,
    Card,
    Button,
    Show,
    Devices,
    Items
  },
  props: {
  },
  async setup() {
    const { loading, actionSave, actionRestore } = useDataMatch()
    const { actionAll: actionItems } = useShowItems('online')
    const route = useRoute()
    const router = useRouter()
    const breadcrumb = [{
      text: 'Mail-In',
      to: {
        name: 'buybackOnline'
      }
    }, {
      text: 'Offer Detail',
      to: {
        name: 'buybackOnlineDetail',
        params: {
          buybackId: route.params.buybackId
        }
      }
    },
    {
      text: 'Data Match'
    }]
    const handleRestore = () => actionRestore(route.params.buybackId, async () => {
      await actionItems(route.params.buybackId, true)
      ElMessage({
        type: 'success',
        message: 'Data match restored'
      })
    })
    const handleSave = () => actionSave(route.params.buybackId, async () => {
      await actionItems(route.params.buybackId, true)
      ElMessage({
        type: 'success',
        message: 'Data match saved'
      })
      router.push({
        name: 'buybackOnlineDetail',
        params: {
          buybackId: route.params.buybackId
        }
      })
    })
    return {
      breadcrumb,
      loading,
      route,
      handleRestore,
      handleSave
    }
  }
})
</script>